@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap" rel="stylesheet');

:root{
  --primary:#1aac83;
  --error:#e7195a;
}

body{
  background: #f1f1f1;
  margin:0;
  font-family:"Poppins";
}

header{
  background:#fff;
  padding: 20px 60px;
  margin:0px 0px;
 
}


header.container{
  max-width:1200px ;
  margin: 0px auto ;
  padding :30px;
  display :flex;
  align-items:center;
  justify-content:space-between;
}

header a{
  color:#333;
  text-decoration: none;
}

.pages{
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;

}

.home {
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap:100px
}

.workout-details{
  background: #fff;
  border-radius: 4px;
  margin :20px auto;
  padding :20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.05);

}

.workout-details h4{
  margin: 0 0 10px 0;
  font-size: 0.6cm;
  color:var(--primary);
}

.workout-details p{
  margin :0;
  font-size:0.4cm;
  color:#555;
}

.workout-details span{
  position: absolute;
  top:20px;
  right:20px;
  cursor:pointer;
  background: #f1f1f1;
  padding :6px;
  border-radius: 50%;
  color:#333
}

label, input{
  display: block;
}

input{
  padding:10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width:100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

form button{
    background: var(--primary);
    border:0;
    color:#fff;
    padding :10px;
    font-family: "Poppins";
    border-radius: 4px;
    cursor: pointer;
}

div.error{
   padding :10px;
   background: #ffefef;
   border: 1px solid var(--error);
   color: var(--error);
   border-radius:4px;
   margin :20px 0;
}